import { Component, ViewChild } from '@angular/core';
import { NavController, Slides, NavParams, IonicPage, MenuController, ModalController, ViewController } from 'ionic-angular';
import { ServicesProvider } from '../../providers/services/services'
import { UtilityProvider } from '../../providers/utility/utility'
 import { Events } from 'ionic-angular';
import { Config } from '../../config';
import { NativePageTransitions, NativeTransitionOptions } from '@ionic-native/native-page-transitions';
import { Scheduler } from 'rxjs/Scheduler';
 
@IonicPage()
@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  @ViewChild('slider') slider: Slides;
  categoryList = [];
  banner = []
  options: NativeTransitionOptions = {
    direction: 'up',
    duration: 400,
    slowdownfactor: 3,
    slidePixels: 20,
    iosdelay: 100,
    androiddelay: 150,
    fixedPixelsTop: 0,
    fixedPixelsBottom: 60
  };
  notificationList: any;
  constructor(public menuCtrl:MenuController,public events: Events,private nativePageTransitions: NativePageTransitions,
    public utility: UtilityProvider, public navCtrl: NavController,
    public navParams: NavParams, public _services: ServicesProvider,
    public viewCtrl: ViewController, public modalCtrl: ModalController) {
      

    }
    ngOnInit(): void {

      
      this.menuCtrl.enable(true);
      var location = this._services.getGeolocation();
      this._services.getCategory().subscribe((Response) => {
        this.categoryList = Response.data
      }, (Error) => {
      })
      this._services.getBanner().subscribe((Response: any) => {
        let result = Response.response.data
        let banner1=[]
        let banner2=[]
        let banner3=[]
        for (let index = 0; index < result.length; index++) {
          let val={}
          if(result[index].bannerindex==1){
            val['image']=result[index].image
            val['bannerindex']=result[index].bannerindex
            val['linkto']=result[index].linkto.linkto
            banner1.push(val)
          }
          if(result[index].bannerindex==2){
            val['image']=result[index].image
            val['bannerindex']=result[index].bannerindex
            banner2.push(val)}
            if(result[index].bannerindex==3){
              val['image']=result[index].image
              val['bannerindex']=result[index].bannerindex
              banner3.push(val)}
            }
            this.banner.push(banner1)
            this.banner.push(banner2)
            this.banner.push(banner3)
          })
        }
        ionViewDidEnter(){
          let obj = {
            devicesid: localStorage.getItem('devices_token'),
            userid: Config.USER._id,
            user_type:'customer',
          }
          this._services.saveDevicesToken(obj).subscribe((response)=>{
          })


          let objdata={
            id: Config.USER._id,
          }
          this._services.getNotification(objdata).subscribe((response)=>{
    
    console.log('====================================')
    console.log(response.response.data )
    console.log('====================================')
    let res=response.response.data;
            this.notificationList=res.length
          },(error)=>{
          })










          setTimeout(() => {

if(localStorage.getItem('addshown')===null){
  this.PromotionModal()
  localStorage.setItem('addshown','1')

}
           
             
           }, 5000);
        }
        goToPostPage(item) {
          this.navCtrl.push("PostPage", { id: item._id ,title:item.title,image:item.image})
          //this.navCtrl.push("TagsPage", { id: item._id })
        }
        goToNotification() {
          this.navCtrl.push("NotificationPage")
        }
        openMenu() {
          this.events.publish('userdetails');
          this.menuCtrl.open();
        }
        goToSearch(){
          this.navCtrl.push("SearchPage")
        }
        saveDevicesToken(){
          let user=JSON.parse(localStorage.getItem('userdetails'));
          let  devicesid=   localStorage.getItem('devices_token');
          if(devicesid !=null){
            let data = {
              devicesid: localStorage.getItem('devices_token'),
              userid: user._id,
              user_type:'customer',
            }
            this._services.saveDevicesToken(data).subscribe((response)=>{
            })
          }
        }
        showPromoterCategory(item){

          console.log(item);
          
          if(item[0].bannerindex==1){

            let item={
              _id:'5b05aefe5c669d0676a6b1f6',
              title:'Restaurant'
            }
            this.navCtrl.push("PostPage", { id: item._id ,title:item.title})




          }
          if(item[0].bannerindex==2){
            let item={
              _id:'5b05aefe5c669d0676a6b1f6',
              title:'Restaurant'
            }
            this.navCtrl.push("PostPage", { id: item._id ,title:item.title})
          }
          if(item[0].bannerindex==3){
            this.navCtrl.push('PromoterCategoryPage') 
          }
        }
        ionViewWillLeave() {
  
        }
        //MODAL FUNCTION
        PromotionModal() {


          let modal = this.modalCtrl.create('PromotionModalPage');
         modal.present();
 
 
         
        }
        dismiss() {
          this.viewCtrl.dismiss();
        }
      }



